import React from 'react';
import { Helmet } from 'react-helmet';
import { useStaticQuery, graphql } from 'gatsby';

export const SEO = () => {
  const {
    site = {},
    file = {},
  } = useStaticQuery(query) || {};

  const { siteMetadata = {} } = site;
  const { publicURL: previewImgURL = '' } = file;

  const {
    title,
    description,
    url,
    twitterUrl
  } = siteMetadata;

  const fullPreviewImgURL = `${url}${previewImgURL}`;

  return (
    <Helmet title={title} >
      <meta name="description" content={description} />

      <meta property="og:type" content="website" />
      <meta property="og:url" content={url} />
      <meta property="og:title" content={title} />
      <meta property="og:description" content={description} />
      <meta property="og:image" content={fullPreviewImgURL} />

      <meta property="twitter:card" content={fullPreviewImgURL} />
      <meta property="twitter:url" content={twitterUrl} />
      <meta property="twitter:title" content={title} />
      <meta property="twitter:description" content={description} />
      <meta property="twitter:image" content={fullPreviewImgURL} />
    </Helmet>
  );
}

const query = graphql`
  query SEO {
    site {
      siteMetadata {
        title
        description
        url
        twitterUrl
      }
    }
    file(relativePath: {eq: "preview.png"}) {
      publicURL
    }
  }
`