import React from 'react';
import PropTypes from 'prop-types';

import * as styles from './styles.module.css';

export const Link = ({
  children,
  href,
}) => (
  <a
    href={href}
    className={styles.root}
  >
    {children}&nbsp;
  </a>
);

Link.propTypes = {
  children: PropTypes.node,
  href: PropTypes.string.isRequired,
};

Link.defaultProps = {
  children: null,
};
