import React, { useState } from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';

import { Link } from '../Link';
import useIntersectionObserver from '../../hooks/useIntersectionObserver';

import * as styles from './styles.module.css';

export const Invitation = ({
  heading,
  info,
  invitationUrl,
  invitationText,
  photoPlacement,
  photoMaxWidthMobile,
  photoMaxWidthDesktop,
  color,
  renderPhoto,
}) => {
  const [hasScrolledIntoView, setHasScrolledIntoView] = useState(false);

  const withPhotoBefore = photoPlacement === 'before';
  const withPhotoAfter = photoPlacement === 'after';
  const withInverseColor = color ==='inverse';

  const photoPlacementStyles = {
    [styles.withPhotoBefore]: withPhotoBefore,
    [styles.withPhotoAfter]: withPhotoAfter,
  };

  const colorStyles = {
    [styles.withInverseColor]: withInverseColor,
  };

  const [ref, entry] = useIntersectionObserver({ root: null, threshold: 0.75 });
  
  if (entry && entry.isIntersecting && !hasScrolledIntoView) {
    setHasScrolledIntoView(true);
  }

  return (
    <section
      className={styles.root}
      ref={ref}
      style={{
        '--vivPhotoMaxWidthMobile': photoMaxWidthMobile,
        '--vivPhotoMaxWidthDesktop': photoMaxWidthDesktop,
      }}
    >
      <div className={cn(styles.content, photoPlacementStyles, colorStyles)}>
        {withPhotoBefore && (
          <div className={styles.photoContainer}>
            {renderPhoto({ placeholder: 'none' })}
          </div>
        )}
        <div className={cn(styles.textContent, photoPlacementStyles, {
          [styles.hasScrolledIntoView]: hasScrolledIntoView,
        })}>
          <h2 className={styles.heading}>{heading}</h2>
          <p className={styles.info}>{info}</p>
          {invitationUrl && (
            <div className={cn(styles.link, colorStyles)}>
              <Link href={invitationUrl}>{invitationText}</Link>
            </div>
          )}
        </div>
        {withPhotoAfter && (
          <div className={styles.photoContainer}>
            {renderPhoto({ placeholder: 'none' })}
          </div>
        )}
      </div>
    </section>
  );
};

Invitation.propTypes = {
  heading: PropTypes.node,
  info: PropTypes.node,
  invitationUrl: PropTypes.string,
  invitationText: PropTypes.string,
  renderPhoto: PropTypes.func,
  photoPlacement: PropTypes.oneOf(['before', 'after']),
  photoMaxWidthMobile: PropTypes.string,
  photoMaxWidthDesktop: PropTypes.string,
  color: PropTypes.oneOf(['primary', 'inverse']),
};

Invitation.defaultProps = {
  heading: null,
  info: null,
  invitationUrl: null,
  invitationText: null,
  renderPhoto: () => {},
  photoPlacement: 'after',
  photoMaxWidthMobile: '22rem',
  photoMaxWidthDesktop: '26rem',
  color: 'primary',
}
