import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';

import { Link } from '../Link';
import * as styles from './styles.module.css';

export const Testimony = ({
  quote,
  renderPhoto,
  shouldInvertBackgroundOnMobile,
  desktopQuoteFontSize,
  tabletQuoteFontSize,
  author,
  position,
  url,
  backgroundTop,
  backgroundBottom,
}) => {
  const getBackgroundStyles = (background) => ({
    [styles.withNeutralBackground]: background === 'neutral',
    [styles.withPrimaryBackground]: background === 'primary',
  });

  return (
    <div className={styles.root}>
      <div
        className={cn(styles.backgroundTop, getBackgroundStyles(backgroundTop))}
      />
      <div className={cn(styles.backgroundBottom, getBackgroundStyles(backgroundBottom))} />
      <div
        className={styles.testimony}
        style={{
          '--vivDesktopQuoteFontSize': desktopQuoteFontSize,
          '--vivTabletQuoteFontSize': tabletQuoteFontSize,
        }}
      >
        <figure className={styles.figure}>
          <div className={styles.pictureContainer}>
            {renderPhoto({
              className: styles.picture
            })}
          </div>
          <div className={cn(styles.content, {
            [styles.withInvertedMobileBackground]: shouldInvertBackgroundOnMobile,
          })}>
            <blockquote className={styles.blockquote}>
              &quot;
              {quote}
              &quot;
            </blockquote>
            <div className={styles.authorInfo}>
              <figcaption>
                <b className={styles.author}>{author}</b>
                <p className={styles.position}>{position}</p>
              </figcaption>
              {url && (
                <div className={cn(styles.link, {
                  [styles.withInvertedMobileBackground]: shouldInvertBackgroundOnMobile,
                })}>
                  <Link href={url}>
                    Read the case study
                  </Link>
                </div>
              )}
            </div>
          </div>
        </figure>
      </div>
    </div>
  );
}

Testimony.propTypes = {
  quote: PropTypes.string.isRequired,
  desktopQuoteFontSize: PropTypes.string,
  tabletQuoteFontSize: PropTypes.string,
  renderPhoto: PropTypes.func,
  shouldInvertBackgroundOnMobile: PropTypes.bool,
  author: PropTypes.string.isRequired,
  position: PropTypes.string.isRequired,
  url: PropTypes.string,
  backgroundTop: PropTypes.oneOf('primary', 'neutral'),
  backgroundBottom: PropTypes.oneOf('primary', 'neutral'),
};

Testimony.defaultProps = {
  classNames: null,
  desktopQuoteFontSize: '1.625rem',
  tabletQuoteFontSize: '1.25rem',
  renderPhoto: null,
  shouldInvertBackgroundOnMobile: false,
  url: null,
  backgroundTop: 'neutral',
  backgroundBottom: 'neutral',
};
